<template>
  <div class="news order">
    <!-- <van-nav-bar title="" left-arrow @click-left="routerBack"/> -->
    <van-tabs v-model="active" sticky line-width="40">
      <van-tab :title="$t('index.all')">
        <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
          <div class="list-item" v-for="item in list" :key="item.id" :class="{history: item.history==1}">
            <div class="item01">
              <span class="text" :class="{history: item.history==1}" v-if="item.status !== 6">{{$t('index.the')}}{{item.stage}}{{$t('index.stage')}}
                <font v-if="item.type==1" :class="{history: item.history==1}">{{$t('index.order')}}</font>
                <font v-if="item.type==2" :class="{history: item.history==1}">{{$t('index.renewal')}}</font>
              </span>
              <span class="text" v-else>{{$t('index.the')}}{{item.stage}}{{$t('index.stage')}}<font>{{$t('index.the')}}{{item.stage-3}}{{$t('index.stage')}}{{$t('index.order')}}</font></span>
              <span class="num" :class="{history: item.history==1}">{{item.status_text}}</span>
            </div>
            <div class="item02">
              <div><span>{{$t('index.time')}}</span><font>{{item.create_time.substr(0,16)}}</font></div>
              <div class="center"><span>{{$t('index.participationAmount')}}(U)</span><font>{{item.num}}</font></div>
              <div v-if="parseFloat(item.num)>=parseFloat(item.invest) && item.status !== 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.15).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)<parseFloat(item.invest) && item.status !== 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.12).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)>=parseFloat(item.invest) && item.status == 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.05).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)<parseFloat(item.invest) && item.status == 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.02).toFixed(2)}}</font></div>
            </div>
            <div class="item02" style="justify-content: flex-start;">
              <div><span>{{$t('index.tokenFee')}}(U)</span><font>{{item.fee}}</font></div>
              <div class="center"><span>{{$t('index.renew')}}</span>
                <font class="color2" v-if="item.order_pledge==0" :class="{history: item.history==1}">{{$t('index.the')}}{{item.append_stage}}{{$t('index.stage')}}{{$t('index.renewedN')}}</font>
                <font v-else :class="{history: item.history==1}">{{$t('index.the')}}{{item.append_stage}}{{$t('index.stage')}}{{$t('index.renewed')}}</font>
              </div>
              <div>
                <span>{{$t('index.renewalType')}}</span>
                <div class="auto-apply">
                  <font v-if="item.append_type==0">{{$t('index.manual')}}</font>
                  <font v-else>{{$t('index.auto')}}</font>
                  <div class="change color1" style="display:flex;" v-if="(item.order_pledge==0 && item.status == 1)||(item.status == 6 && item.order_pledge==0) && allStatus!='2'" @click="changeAuto(item)">&nbsp;{{$t('index.switch')}}</div>
                </div>
              </div>
            </div>
            <div class="item03" v-if="allStatus!='2'">
              <van-button v-if="item.status == 6 && item.order_pledge==0" class="common-btn common-btn3" @click="openShow2(item)">{{$t('index.recycle')}}</van-button>
              <van-button v-if="(item.append_type == 0 && item.order_pledge==0 && item.status == 1) || (item.append_type == 0 && item.order_pledge==0  && item.status == 6)" class="common-btn ml" @click="openShow(item)">{{$t('index.manual')}}</van-button>
            </div>
          </div>
        </van-list>
      </van-tab>
      <van-tab :title="$t('index.pendingSettlement')">
        <van-list class="list" v-model="loading2" :finished="finished2" finished-text="" @load="onLoad2">
          <div class="list-item" v-for="item in list2" :key="item.id">
            <div class="item01">
              <span class="text" v-if="item.status !== 6">{{$t('index.the')}}{{item.stage}}{{$t('index.stage')}}
                <font v-if="item.type==1">{{$t('index.reserve')}}</font>
                <font v-if="item.type==2">{{$t('index.renewal')}}</font>
              </span>
              <span class="text" v-else>{{$t('index.the')}}{{item.stage}}{{$t('index.stage')}}<font>{{$t('index.the')}}{{item.stage-3}}{{$t('index.stage')}}{{$t('index.order')}}</font></span>
              <span class="num">{{item.status_text}}</span>
            </div>
            <div class="item02">
              <div><span>{{$t('index.time')}}</span><font>{{item.create_time.substr(0,16)}}</font></div>
              <div class="center"><span>{{$t('index.participationAmount')}}(U)</span><font>{{item.num}}</font></div>
              <div v-if="parseFloat(item.num)>=parseFloat(item.invest) && item.status !== 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.15).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)<parseFloat(item.invest) && item.status !== 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.12).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)>=parseFloat(item.invest) && item.status == 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.05).toFixed(2)}}</font></div>
              <div v-else-if="parseFloat(item.num)<parseFloat(item.invest) && item.status == 6"><span>{{$t('index.profit')}}</span><font>{{(item.num*0.02).toFixed(2)}}</font></div>
            </div>
            <div class="item02" style="justify-content: flex-start;">
              <div><span>{{$t('index.tokenFee')}}(U)</span><font>{{item.fee}}</font></div>
              <div class="center"><span>{{$t('index.renew')}}</span>
                <font class="color2" v-if="item.order_pledge==0">{{$t('index.the')}}{{item.append_stage}}{{$t('index.stage')}}{{$t('index.renewedN')}}</font>
                <font v-else>{{$t('index.the')}}{{item.append_stage}}{{$t('index.stage')}}{{$t('index.renewed')}}</font>
              </div>
              <div>
                <span>{{$t('index.renewalType')}}</span>
                <div class="auto-apply">
                  <font v-if="item.append_type==0">{{$t('index.manual')}}</font>
                  <font v-else>{{$t('index.auto')}}</font>
                  <div class="change color1" style="display:flex;" v-if="(item.order_pledge==0 && item.status == 1)||(item.status == 6 && item.order_pledge==0) && allStatus!='2'" @click="changeAuto(item)">&nbsp;{{$t('index.switch')}}</div>
                </div>
              </div>
            </div>
            <div class="item03" v-if="allStatus!='2'">
              <van-button v-if="item.status == 6 && item.order_pledge==0" class="common-btn common-btn3" @click="openShow2(item)">{{$t('index.recycle')}}</van-button>
              <van-button v-if="(item.append_type == 0 && item.order_pledge==0 && item.status == 1) || (item.append_type == 0 && item.order_pledge==0  && item.status == 6)" class="common-btn ml" @click="openShow(item)">{{$t('index.manual')}}</van-button>
            </div>
          </div>
        </van-list>
      </van-tab>
      <van-tab :title="$t('index.completed')">
        <van-list class="list" v-model="loading3" :finished="finished3" finished-text="" @load="onLoad3">
          <div class="list-item" v-for="item in list3" :key="item.id" :class="{history: item.history==1}">
            <div class="item01">
              <span class="text">{{$t('index.the')}}{{item.stage}}{{$t('index.stage')}}
                <font v-if="item.type==1">{{$t('index.reserve')}}</font>
                <font v-if="item.type==2">{{$t('index.renewal')}}</font>
              </span>
              <span class="num">{{item.status_text}}</span>
            </div>
            <div class="item02">
              <div><span>{{$t('index.time')}}</span><font>{{item.create_time.substr(0,16)}}</font></div>
              <div class="center"><span>{{$t('index.participationAmount')}}(U)</span><font>{{item.num}}</font></div>
              <div><span>{{$t('index.periodProfit')}}</span><font>{{(item.rate*item.num).toFixed(2)}}</font></div>
            </div>
            <div class="item02" style="justify-content: flex-start;">
              <div style="align-items: flex-start;"><span>{{$t('index.tokenFee')}}(U)</span><font>{{item.fee}}</font></div>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
    <!--  -->
    <van-dialog v-model="show2" width="320" :showConfirmButton="false">
      <img src="../../assets/img/warning.png" alt="">
      <div class="title" style="margin-top:10px;">{{$t('index.recyclingReminder')}}</div>
      <div class="text" v-if="localeLanguage == 'en-us'">If you renew the investment in this period, you can still get 5% profit of the previous investment (delayed renewal). If you choose not to renew the investment but directly reclaim it, the order principal will be returned to the balance after two periods, and a 5% management fee will be deducted. Please choose carefully!</div>
      <div class="text" v-else>5%5%</div>
      <div class="button" style="margin-bottom:20px;">
        <van-button class="common-btn cancel" @click="recoverSubmit()">{{$t('index.continueRecycling')}}</van-button>
        <van-button class="common-btn" @click="goAgain()">{{$t('index.renewFirst')}}</van-button>
      </div>
      <van-icon class="close" name="cross" @click="show2=false"/>
    </van-dialog>
    <!-- -->
    <van-popup class="index-popup" v-model="show" position="bottom" closeable>
      <div class="title">{{$t('index.the')}}{{userStage.append_stage}}{{$t('index.stage')}}{{$t('index.renewal')}}</div>
      <div class="from">
        <div class="input">
          <div class="left">
            <img src="../../assets/img/usdt.png" alt="">
            <van-field class="" v-model="number" type="number"/>
          </div>
          <div class="right">
            <div class="text">USDT</div>
            <van-dropdown-menu :overlay="false">
              <van-dropdown-item v-model="typeId" :options="typeList" @change="changeAppend" @close="changeAppend"/>
            </van-dropdown-menu>
          </div>
        </div>
      </div>
      <div class="money">{{$t('index.available')}}<span>{{userInfo.usdt}} USDT</span></div>
      <div class="auto">
        <router-link :to="{path: '/Type', query:{ type: '1'}}" class="title2">
          <div class="auto-text">
            <div>{{$t('index.the')}}{{userStage.append_stage+3}}{{$t('index.stage')}}{{$t('index.enable')}}&nbsp;<img src="../../assets/img/rules.png" alt=""></div>
            <span style="font-size:12px">&nbsp;({{$t('index.default')}})</span></div>
        </router-link>
        <van-switch v-model="autoDouble" active-color="#1AC3E9" inactive-color="#4F5570" size="20px"/>
      </div>
     <div class="details"><div style="color: #6D738D;">{{$t('index.investmentType')}}</div>
        <router-link :to="{path: '/Type', query:{ type: '2'}}" v-if="userStage.status==6">{{$t('index.deferredRenewal')}} {{$t('index.profit2')}}-10%<img src="../../assets/img/rules.png" alt=""></router-link>
        <router-link :to="{path: '/Type', query:{ type: '2'}}" v-else-if="parseFloat(number)>=parseFloat(userStage.invest)">{{$t('index.standardInvestment')}} {{$t('index.profit2')}}15%<img src="../../assets/img/rules.png" alt=""></router-link>
        <router-link :to="{path: '/Type', query:{ type: '2'}}" v-else-if="parseFloat(number)<parseFloat(userStage.invest)">{{$t('index.secondaryInvestment')}} {{$t('index.profit2')}}12%<img src="../../assets/img/rules.png" alt=""></router-link>
      </div>
      <div class="details"><div>{{$t('index.tokenDestroy')}}</div><div>{{(number*0.01).toFixed(2)}} USDT</div></div>
      <div class="details"><div>{{$t('index.earningsPeriod')}}</div><div>{{$t('index.the')}}{{userStage.append_stage+3}}{{$t('index.stage')}}{{$t('index.end')}}</div></div>
      <div class="details"><div>{{$t('index.profit')}}</div><div>{{profit}} USDT</div></div>
      <div class="details"><div>{{$t('index.totaRevenue')}}</div>
        <div v-if="parseFloat(number)>0" style="color:#1AC3E9; font-size: 16px;">{{(parseFloat(number)+parseFloat(profit)).toFixed(2)}} USDT</div>
        <div v-else style="color:#1AC3E9; font-size: 16px;">0.00 USDT</div>
      </div>
      <div class="details"><div>&nbsp;</div><div style="color:#4F5570;">*{{$t('index.note')}}</div></div>
      <van-button v-if="true" class="common-btn" @click="checkSubmit()">{{$t('index.renewNow')}}</van-button>
    </van-popup>
    <!--  -->
    <van-dialog v-model="show8" width="320" :showConfirmButton="false">
      <img class="warning" src="../../assets/img/warning01.png" alt="">
      <div class="close" @click="show8=false"><van-icon name="cross" /></div>
      <div class="title" style="padding-top:35px;">{{$t('index.reminder')}}</div>
      <div class="text" v-if="localeLanguage == 'en-us'">Because your investment amount in this period is lower than the minimum amount {{userStage.invest}} U in the standard investment range, it is currently a secondary investment, and the profit will be reduced to 12%. If you want to maximize profits, the investment amount must meet the standard investment range.</div>
      <div class="text" v-else>{{userStage.invest}} U12%</div>
      <div class="button" style="margin-bottom:20px;">
        <van-button class="common-btn cancel" @click="submit()">{{$t('index.continueAmount')}}</van-button>
        <van-button class="common-btn" @click="show8=false">{{$t('index.modify')}}</van-button>
      </div>
    </van-dialog>
    <!--  -->
    <van-dialog v-model="show9" width="320" :showConfirmButton="false">
      <img class="warning" src="../../assets/img/warning01.png" alt="">
      <div class="close" @click="show9=false"><van-icon name="cross" /></div>
      <div class="title" style="padding-top:35px;">{{$t('index.reminder2')}}</div>
      <div class="text" v-if="localeLanguage == 'en-us'">The {{userStage.append_stage}} smart chip you invested in will be renewed in the next {{userStage.append_stage+3}} stage. Due to the increase in the scope of the standard investment during this period, if your balance is insufficient, the renewal order may become a secondary investment, and the profit will be reduced to 12%. This reminds you to recharge in time to ensure maximum profit.</div>
      <div class="text" v-else>{{userStage.append_stage}}{{userStage.append_stage+3}}12%</div>
      <div class="button" style="margin-bottom:20px;">
        <van-button class="common-btn cancel" to="/Recharge">{{$t('index.rechargeNow')}}</van-button>
        <van-button class="common-btn" @click="show9=false">{{$t('index.renewNow')}}</van-button>
      </div>
    </van-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      localeLanguage: localStorage.getItem('localeLanguage'),
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      active: 0,
      list: [],
      loading: false,
      finished: false,
      page: 1,
      list2: [],
      loading2: false,
      finished2: false,
      page2: 1,
      list3: [],
      loading3: false,
      finished3: false,
      page3: 1,
      userStage: {}, //
      show: false, // xutou
      show2: false, // 
      number: '10.00',
      typeId: 1,
      typeList: [
        {value:1, text: this.$t('index.goal1')},{value:2, text: this.$t('index.investmentMin')},{value:3, text: this.$t('index.sameInvestment')}
      ],
      autoDouble: true,
      show8: false,
      show9: false,
      stageTips: [11,12,13,21,22,23,31,32,33,36,37,38,41,42,43],
      allStatus: '', //
    }
  },
  computed: {
    profit() {
      if(parseFloat(this.number)>=parseFloat(this.userStage.invest)) {
        return (this.number*0.15).toFixed(2);
      }
      if(parseFloat(this.number)<parseFloat(this.userStage.invest)) {
        return (this.number*0.12).toFixed(2);
      }
    }
  },
  methods: {
    // :1,2,3,4,5,6
    onLoad() {
      const params = {
        page: this.page,
        status: '',
      }
      this.page++;
      this.$http('/stage/order', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        if(response.data.msg == '2') {
          this.allStatus = '2';
        }
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
    onLoad2() {
      const params = {
        page: this.page2,
        status: '1',
      }
      this.page2++;
      this.$http('/stage/order', {params: params}).then(response => {
        let res = response.data.data;
        // console.log(res);
        res.data.forEach(element => {
          this.list2.push(element);
        });
        this.loading2 = false;
        if (res.data.length < res.per_page) {
          this.finished2 = true;
        }
      });
    },
    onLoad3() {
      const params = {
        page: this.page3,
        status: '2',
      }
      this.page3++;
      this.$http('/stage/order', {params: params}).then(response => {
        let res = response.data.data;
        // console.log(res);
        res.data.forEach(element => {
          this.list3.push(element);
        });
        this.loading3 = false;
        if (res.data.length < res.per_page) {
          this.finished3 = true;
        }
      });
    },
    openShow2(stage) {
      this.show2 = true;
      this.userStage = stage;
    },
    // {{$t('index.recycle')}}
    recoverSubmit() {
      const formData = {
        id: this.userStage.id,
      }
      this.$http.post('/stage/refund', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        if(res.code === 0) {
          this.show2 = false;
          if(this.active == 0) {
            this.list = [];
            this.page = 1;
            this.onLoad();
          }
        }
      })
    },
    // 
    goAgain() {
      this.show2 = false;
      setTimeout(() => {
        this.openShow(this.userStage);
      }, 500);
    },
    // 
    openShow(stage) {
      this.show = true;
      this.userStage = stage;
      if(this.typeId == 1) {
        this.number = (this.userStage.total/100).toFixed(2);
      }else if(this.typeId == 2) {
        this.number = parseFloat(this.userStage.invest).toFixed(2);
      }else if(this.typeId == 3) {
        this.number = parseFloat(this.userStage.num).toFixed(2);
      }
    },
    // 
    changeAppend() {
      if(this.typeId == 1) {
        this.number = (this.userStage.total/100).toFixed(2);
      }else if(this.typeId == 2) {
        this.number = parseFloat(this.userStage.invest).toFixed(2);
      }else if(this.typeId == 3) {
        this.number = parseFloat(this.userStage.num).toFixed(2);
      }
    },
    // 
    selectType(value) {
      this.append = value;
    },
    // 
    checkSubmit() {
      if(parseFloat(this.number) > parseFloat(this.userInfo.usdt)) {
        this.$toast(this.$t('index.orderTips'));
        return;
      }
      if(parseFloat(this.number) < parseFloat(this.userStage.invest) && parseFloat(this.number)>=10) {
        this.show8 = true;
      }else{
        this.submit();
      }
    },
    // 
    submit() {
      this.show8 = false;
      let append = 1;  //
      if(!this.autoDouble) {
        append = 0;
      }
      const formData = {
        id: this.userStage.id,
        num: this.number,
        append: append,
      }
      this.$http.post('/stage/append', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        if(res.code === 0) {
          this.show = false;
          if(this.active == 0) {
            this.list = [];
            this.page = 1;
            this.onLoad();
          }else{
            this.list2 = [];
            this.page2 = 1;
            this.onLoad2();
          }
          if(this.stageTips.indexOf(this.userStage.append_stage+3) != -1) {
            setTimeout(() => {
              this.show9 = true;
            }, 200);
          }
        }
      })
    },
    //  01
    changeAuto(item) {
      let type = 0;
      if(item.append_type == 0) {
        type = 1;
      }
      const formData = {
        id: item.id,
        append: type,
      }
      this.$http.post('/stage/type', formData).then((response) => {
        const res = response.data;
        if(res.code === 0) {
          item.append_type = type;
          if(type == 0) {
            this.openShow(item);
          }else{
            this.$toast(res.msg);
          }
        }else{
          this.$toast(res.msg);
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.van-tabs{ margin-top: 0;}
.list{ width: 100%; padding: 0 4% 100px 4%; min-height: 100%; display: flex; flex-direction: column; }
