.list-item .item01{ display: flex; justify-content: space-between; line-height: 26px;}
.list-item .item01 .text{ color: #E5EDFB; font-size: 16px;}
.list-item .item01 .text font{ color: #B9BFD9; font-size: 14px;}
.list-item .item01 .num{ color: #1AC3E9; font-size: 15px;}
.list-item .item02{ margin-top: 5px; display: flex; justify-content: space-between;}
.list-item .item02>div{ display: flex; width: 33.33%; flex-direction: column;}
.list-item .item02>div:last-of-type{align-items: flex-end;}
.list-item .item02>div font{ color: #B9BFD9;  margin-top: 2px; }
.list-item .common-btn{ height: 30px; width: 80px; font-size: 12px;}
.list-item .item03{ display: flex; justify-content: flex-end; margin-top: 5px;}
.list-item .common-btn3{ background:rgba(229,238,252,0.1); color: #B9BFD9; box-shadow: none;}
.color1{ color: #19C3E9!important;}
.color2{ color: #cc3b3b!important;}
.list-item .item04{ margin-top: 10px; display: flex; justify-content: space-between; line-height: 22px;}
.list-item .item04>div{ display: flex; width: 33.33%; flex-direction: column;}
.list-item .item04>div:last-of-type{ width: 66%;}
.list-item .item04>div font{ color: #B9BFD9;}
.list-item .item02 .center{ text-align: center;}
.auto-apply{ display: flex;}
.change{ position: relative; width: 40px; text-align: center; margin-top: 2px;}
.change::after{ content: ""; position: absolute; right: 0; top: 7px; border-right:4px solid transparent; border-left:4px solid transparent; border-top:5px solid #19C3E9;}
.ml{ margin-left: 15px;}

.van-dialog{ background: #121A33; padding-bottom: 20px; border-radius:10px; color: #B9BFD9; overflow: visible; }
.van-dialog .title{ padding: 20px 0; color: #E5EEFC; font-size: 17px; text-align: center;}
.van-dialog .text{ padding: 0 4%; font-size: 12px; text-align: left; line-height: 22px;}
.van-dialog .button{ margin-top: 20px; padding: 0 10%; display: flex; justify-content: space-between;}
.van-dialog .button .common-btn{  width: 100px; height: 34px; line-height: 34px; font-size: 14px; border-radius: 5px;}
.van-dialog .button .cancel{ background: #19223E; color: #B9BFD9; box-shadow: none;}
.other{ font-size: 12px; color: #4F5570; padding: 10px 0 20px 0; text-align: center;}
.van-dialog img{ position: absolute; left: 50%; top: -26px; width: 60px; margin-left: -30px; z-index: 100;}
.van-radio-group{ padding: 20px 0; display: flex; justify-content: center; font-size: 12px;}
.close{ position: absolute; right: 4%; top: 10px; font-size: 22px;}

.van-popup{ padding: 16px 4% 20px 4%; background: #121A33; border-radius:10px 10px 0 0;}
.van-popup .title{ font-size: 16px; color: #B9BFD9;}
.van-popup .title2{ font-size: 16px; padding: 10px 0 10px 0; display: flex; align-items: center; color: #B9BFD9;}
.van-popup .title2 img{ width: 16px; margin-left: 4px;}
.van-popup .title2 span{ color: #6D738D;}
.from{ margin-top: 10px; background: #19223E; width: 100%; padding: 0 4%; border-radius:10px; border:1px solid #4F5570;}
.from .input{ padding: 10px 0; display: flex; justify-content: space-between; color: #fff; font-size: 14px; align-items: center; }
.from .input .left{ display: flex; align-items: center;}
.from .input img{ width: 25px; height: 25px;}
.from .input .left .van-cell{ padding: 0 0 0 10px; color: #2B303D;}
.from .input .right{ display: flex; align-items: center;}
.from .input .text{ padding: 0 20px; position: relative; color: #B9BFD9;}
.from .input .text::after{ content: ""; position: absolute; top: 0; right: 6px; height: 100%; width: 0; border-left: solid 1px #263051;}
.from .van-field{ border: 0;}
.money{ padding: 10px 0 0 0; font-size: 14px; line-height: 20px;}
.money span{ color: #B9BFD9;}
.type{ display: flex; justify-content: space-between; flex-wrap: wrap;}
.type li{ width: 48%; display: flex; justify-content: space-between; border: solid 1px #4F5570; padding: 10px 4%; margin-bottom: 10px; border-radius: 5px; align-items: center; font-size: 14px;}
.type li img{ width: 20px;}
.type li.active{border: solid 1px #B9BFD9; color: #E5EEFC;}
.type2 li{ margin-bottom: 0; margin-top: 10px;}
.van-popup .details{ padding: 6px 0; display: flex; justify-content: space-between; }
.van-popup .details>div:last-of-type, .van-popup .details>a:last-of-type{ color: #B9BFD9; display: flex; align-items: center;}
.van-popup .details img{ width: 16px; margin-left: 4px;}
.common-btn{ margin-top: 10px;}

.auto{ padding: 2px 0; margin: 10px 0; display: flex; align-items: center; justify-content: space-between; border-top: solid 1px #263050; border-bottom: solid 1px #263050;}
.auto-text{ display: flex; flex-direction: column;  justify-content: center;}
.auto-text span{ display: flex; align-items: center;}
.auto-text>div{ display: flex; align-items: center;}

.van-radio-group{ padding: 0; }

.btns{ margin-top: 10px; display: flex; background: #25335d; border-radius: 22px; overflow: hidden;}
.btns-item{ height: 30px; line-height: 30px; width: 60px; text-align: center; color: #B9BFD9;}
.btns .active{ background: linear-gradient(90deg,rgba(29,213,230,1) 0%,rgba(12,96,252,1) 100%); color: #fff!important;}

.history{ background: #242733!important; color: #656b7d!important;}
/* .history *{color: #696b71!important;} */
</style>
