import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    showLoading: false,
  },
  // computed
  getters: {
    showLoading(state){
      return state.showLoading
    }
  },
  // state
  mutations: {
    SHOWLOADING(state){
      state.showLoading = true;
    },
    HIDELOADING(state){
      state.showLoading = false;
    }
  },
  //  mutation
  actions:{
    showloader:({ commit }) => {
      commit('SHOWLOADING')
    },
    hideloader:({ commit }) => {
      commit('HIDELOADING')
    },
  },
})