<template>
  <div class="property">
    <div class="top">
      <router-link class="total" to="/WalletLog">
        <div class="title">{{$t('index.available')}}USDT</div>
        <div class="num">{{userInfo.usdt}}<van-icon name="play" /></div>
      </router-link>
      <div class="list">
        <router-link to="/Recharge"><img src="../../assets/img/coin01.png" alt=""><div>{{$t('index.recharge')}}</div></router-link>
        <router-link to="/Withdraw"><img src="../../assets/img/coin02.png" alt=""><div>{{$t('index.withdrawal')}}</div></router-link>
        <router-link to="/Transfer"><img src="../../assets/img/coin03.png" alt=""><div>{{$t('index.transfer')}}</div></router-link>
      </div>
    </div>
    <div class="coin-list">
      <router-link class="coin" tag="div" :to="{path:'/RechargeLog', query: {type:1}}">
        <div><img src="../../assets/img/coin04.png" alt=""><span>{{$t('index.depositRecord')}}</span></div>
        <div><van-icon name="arrow"/></div>
      </router-link>
      <router-link class="coin" tag="div" :to="{path:'/WithdrawLog', query: {type:2}}">
        <div><img src="../../assets/img/coin05.png" alt=""><span>{{$t('index.withdrawalRecord')}}</span></div>
        <div><van-icon name="arrow"/></div>
      </router-link>
      <router-link class="coin" tag="div" :to="{path:'/TransferLog', query: {type:2}}">
        <div><img src="../../assets/img/coin06.png" alt=""><span>{{$t('index.transferRecords')}}</span></div>
        <div><van-icon name="arrow"/></div>
      </router-link>
    </div>
    <!-- <div class="auto" @click="showPopup">
      <div class="item">
        <div class="left">
          <font></font>
          <img src="../../assets/img/rules.png" alt="">
        </div>
        <van-switch v-model="autoDouble" disabled  active-color="#1AC3E9" inactive-color="#4F5570" size="20px"/>
      </div>
    </div>
    <van-popup v-model="show" position="bottom" closeable>
      <div class="title"></div>
      <div class="content"></div>
      <van-button class="common-btn" v-if="autoDouble" @click="submit"></van-button>
      <van-button class="common-btn" v-else @click="submit"></van-button>
    </van-popup> -->
    <app-bar></app-bar>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userInfo: {usdt: '0.0000'},
      show: false,
      autoDouble: false,
      autoCash: false
    }
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    // 
    getUserInfo() {
      this.$http("/user/info").then(response => {
        const res = response.data.data;
        console.log(res);
        this.userInfo = res;
        localStorage.setItem("userInfo", JSON.stringify(res));
      });
    },
    // 
    showPopup() {
      this.show = true;
    },
    // 
    submit() {
      const formData = {
        auto: '1',
      }
      this.$http.post('/user/auto', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        // console.log(res)
        if(res.code === 0) {
          this.show = false;
          this.getUserInfo();
        }
      })
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.property{ padding-top: 20px;}
.top{ margin: 0 4%; width: 92%; display: flex; flex-direction: column; color: #E5F8FC; background:linear-gradient(90deg,rgba(12,96,252,1),rgba(29,213,230,1)); box-shadow:0px 5px 20px 0px rgba(0,217,253,0.14); border-radius: 10px;}
.top .total{ display: flex; padding-bottom: 10px; flex-direction: column; color: #E5F8FC;}
.top .title{  display: flex; margin-top: 20px; font-size: 13px; padding-left: 4%; justify-content: center;}
.top .num{ display: flex; padding-left: 4%; font-size: 24px; justify-content: center; align-items: center;}
.top .list{ display: flex; justify-content: space-between; padding: 15px 4%; }
.top .list>a{ width: 33.33%; display: flex; flex-direction: column; align-items: center; justify-content: center; color: #E5F8FC;}
.top .list>a img{ width: 40px; margin-bottom: 4px;}

.coin-list{ margin: 20px 4% 0 4%; width: 92%; padding: 0 4%; background: #121A33; border-radius:5px; }
.coin{ padding: 18px 0; display: flex; justify-content: space-between; font-size: 15px; color: #B9BFD9; border-bottom: solid 1px #19223E;}
.coin img{ width: 20px; margin-right: 4px;}
.coin>div{  display: flex; align-items: center;}
.coin>div .van-icon{ display: flex; align-items: center; color: #4F5570;}

.auto{ margin: 80px 4% 0 4%; width: 92%; padding: 15px 4%; background: #121A33; color: #B9BFD9; border-radius:5px; font-size: 15px;}
.auto img{ width: 20px; margin-left: 6px;}
.auto .item{display: flex; justify-content: space-between; }
.auto .item .left{ display: flex; align-items: center; }
.van-popup{ padding: 20px 4%; border-radius:8px 8px 0 0; bottom: 0; right: 0; background: #121A33;}
.van-popup .title{ color: #B9BFD9; font-size: 16px; }
.van-popup .content{ padding: 10px 0 40px 0; line-height: 20px;}
.close{ font-size: 22px; position: absolute; right: 4%; top: 10px;}
</style>
