<template>
  <div class="share">
    <!-- <van-nav-bar title="" left-arrow @click-left="routerBack" style="background:none;"/> -->
    <div class="rules" @click="show=true"><img src="../../assets/img/rules2.png" alt=""></div>
    <div class="part">
      <div class="QRcode">
        <div id="qr"></div>
      </div>
    </div>
    <div class="list-item">
      <div class="code"><font>{{$t('index.inviteCode')}}</font><span class="font01">{{userInfo.invite_code}}</span></div>
      <div v-clipboard:copy="userInfo.invite_code" v-clipboard:success="onCopy" v-clipboard:error="onError"><img src="../../assets/img/copy2.png" alt=""></div>
    </div>
    <div class="list-item" style="background:linear-gradient(90deg,rgba(255,89,0,1),rgba(253,185,89,1));">
      <div class="code"><font>{{$t('index.inviteLink')}}</font><span style="font-size: 12px;">{{url}}</span></div>
      <div v-clipboard:copy="url" v-clipboard:success="onCopy" v-clipboard:error="onError"><img src="../../assets/img/copy2.png" alt=""></div>
    </div>
    <van-popup v-model="show" position="bottom" closeable>
      <div class="title">{{$t('index.shareInfo')}}</div>
      <div class="content" v-if="localeLanguage=='en-us'">
Invite your friends to participate in AMB smart chip to share the surprise experience of smart contract financing!<br>
Your friend needs to download imtoken wallet, click "Browse" in the menu below, enter the invitation link you sent in the search bar (the content in the orange column above can be copied), or click the scan code function in the search bar to scan the QR code above to open AMB smart chip.<br>
You can collect smart chip DAPP or open smart chip DAPP directly in the recent column.<br>
When your friend enters smart chip for the first time, you need to enter your invitation code and bind the recommendation relationship.<br>
Share wisdom and happiness, share financial income! AMB smart chip welcome global smart chip talent to join!<br></div>
<div class="content" v-else>
AMB<br>
imTokenAMB<br>
DappDapp<br>
<br>
AMB<br></div>
    </van-popup>
    <van-button class="common-btn" @click="shareAction">{{$t('index.shareFriend')}}</van-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      localeLanguage: localStorage.getItem('localeLanguage'),
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      config: JSON.parse(localStorage.getItem('config')),
      url: '',
      show: false,
    }
  },
  mounted() {
    console.log(this.config);
    // this.url = this.config.web_url+'?invite_code='+this.userInfo.invite_code;
    this.url = this.config.web_url;
    // 
    this.info = JSON.parse(localStorage.getItem("share"));
    let width = 144;
    let qr = new QRCode(document.getElementById("qr"), {
      width: width,
      height: width,
      colorDark: "#000000",
      colorLight: "rgba(255,255,255,1)"
    });
    qr.makeCode(this.url);
  },
  methods: {
    // imtoken
    shareAction() {
      imToken.callAPI('native.share', {
        title: 'AMB',
        message: 'AMB',
        url: this.url,
      }, function (err, ret) {
        if(err) {
          alert(err.message)
        } else {
          alert('share success!')
        }
      })
    },
    //
    onCopy: function (e) {
      this.$toast(this.$t('index.copyS'));
    },
    onError: function (e) {
      this.$toast(this.$t('index.copyF'));
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.share{ padding-bottom: 80px; background: #0D1321 url(../../assets/img/share.png) left top no-repeat; background-size: 100%;}
.title{ width: 100%; margin-top: 30px; text-align: center; }
.title img{ width: 48%;}
.part{ height: 190px; margin: 32vw 4% 40px 4%; width: 92%; padding: 10px 8%; display: flex; flex-direction: column; align-items: center;}
.share-code{ margin-top: 10px;}
.QRcode{ text-align: center;font-size: 14px; border-radius: 4px; border: solid 2px #fff;}
.QRcode #qr{ background: #fff; height: 160px; width: 160px; padding: 4px; display: flex; justify-content: center;border-radius: 4px;}
.code{ display: flex; flex-direction: column;}
.common-btn{position: fixed; left: 4%; width: 92%; bottom: 20px; }
.tips{ padding: 10px 4%;}

.list-item{ font-size: 13px; padding: 16px 3%; width: 92%; margin: 15px 4% 0 4%; display: flex; color: #E5F7FB; justify-content: space-between; align-items: center; line-height: 26px; background:linear-gradient(90deg,rgba(11,95,251,1),rgba(29,213,229,1)); border-radius:10px;}
.font01{ font-size: 24px; }
.list-item img{ width: 20px;}

.rules{ right: 6%; top: 26px; position: fixed;}
.rules img{ width: 20px;}
.van-popup{ padding: 20px 4%; border-radius:8px 8px 0 0; bottom: 0; right: 0; background: #121A33;}
.van-popup .title{ color: #B9BFD9; font-size: 16px; margin-top: 10px;}
.van-popup .content{ padding: 10px 0 40px 0; max-height: 470px; overflow-y: scroll; line-height: 24px;}
.close{ font-size: 22px; position: absolute; right: 4%; top: 10px;}
</style>
